/* formatações do card e foto*/
.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  padding: 10px;
  width: 300px;
  text-align: center;
}

.foto{ 
  width: 100%;
  border-radius: 4px;
  /*objectfit: cover;
  object-position: center; */
}

.linha {
  border: 1px solid #000;
width: 300px;
}